// ** React Imports
import { Link } from 'react-router-dom'
import { useState, useEffect, Fragment } from 'react'
import Avatar from '@components/avatar'
import Select from 'react-select'
// ** Third Party Components
import { Can } from '@src/utility/context/Can.js'
import ReactPaginate from 'react-paginate'
import { Download, MoreVertical, Send, Eye, Edit, Trash, Copy, Trash2, Save, Upload } from 'react-feather'
import SendInvoiceSidebar from '../shared-sidebar/SidebarSendInvoice'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import axios from '@src/configs/axios/axiosConfig'
// ** Reactstrap Imports
import {
  Button, Input, Row, Col, Card, CardBody, Label,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  Badge,
  UncontrolledTooltip,
  Table,
  DropdownItem
} from 'reactstrap'

import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
// ** Store & Actions
import { getData, exportInvoice, deleteInvoice, deleteInvoiceBulk } from '../store'
import { useDispatch, useSelector } from 'react-redux'

// ** Styles
import '@styles/react/apps/app-invoice.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import { selectThemeColors } from '@utils'
import moment from 'moment'
const MySwal = withReactContent(Swal)
const statusOptions = [{ name: 'Select All', id: '' }, { name: 'Pending', id: 1 }, { name: 'In progress', id: 2 }, { name: 'Completed', id: 3 }, { name: 'Sent to Review', id: 6 }, { name: 'Request Changes', id: 7 }]
const statusObj = [
  '',
  'light-primary',
  'light-secondary',
  'light-success',
  'light-danger',
  'light-warning',
  'light-info',
  'light-success'
]
const paymentstatusObj = [
  { id: '', name: "Select All" },
  { id: '4', name: "Draft" },
  { id: '5', name: "Pending", color: 'light-danger' },
  { id: '6', name: "Partially Paid", color: 'light-warning' },
  { id: '7', name: 'Paid', color: 'light-success' },
  { id: '11', name: 'Sent', color: 'light-secondary' }
]

const CustomHeader = ({ handleFilter, value, handlePerPage, rowsPerPage, exportList, setCurrentTaskStatus, setCurrentStatus, currentStatus, currentTaskStatus, selecteIds }) => {

  const dispatch = useDispatch()

  const deleteInvoicefun = () => {

    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await dispatch(deleteInvoiceBulk(selecteIds))
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'Invoice has been deleted.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
      }
    })
  }


  return (
    <div className='invoice-list-table-header w-100 py-2 px-1'>
      <Row className='px-1'>
        <Col lg='3' sm='12' className='d-flex align-items-center px-0 px-sm px-lg-1'>
          <div className='d-flex align-items-center me-2'>
            <label htmlFor='rows-per-page'>Show</label>
            <Input
              type='select'
              id='rows-per-page'
              value={rowsPerPage}
              onChange={handlePerPage}
              className='form-control ms-50 pe-3'
            >
              <option value='10'>10</option>
              <option value='25'>25</option>
              <option value='50'>50</option>
            </Input>
          </div>

          {selecteIds.length > 0 &&
            <Trash2 size={16} className='text-danger' onClick={() => { deleteInvoicefun() }} />
          }
        </Col>

        <Col
          lg='9' sm='12'
          className='d-flex align-items-sm-center justify-content-xl-end justify-content-start flex-xl-nowrap flex-wrap flex-sm-row flex-column pe-xl-1 p-0 mt-xl-0 mt-1'
        >
          <Col md='2' sm='12' className='ms-0 m-l-50 me-50'>
            <Select
              theme={selectThemeColors}
              isClearable={false}
              className='react-select'
              placeholder={'Select Status'}
              classNamePrefix='select'
              options={paymentstatusObj}
              value={currentStatus}
              getOptionLabel={(option) => option.name}
              getOptionValue={(option) => option.id}
              onChange={async data => {
                setCurrentStatus(data)
              }}
            />
          </Col>
          {(parseInt(currentStatus.id) === 4 || currentStatus.id === '') &&
            <Col md='2' sm='12' className='ms-0 m-lg-50 mt-50 me-50'>
              <Select
                placeholder={'Task Status'}
                theme={selectThemeColors}
                isClearable={false}
                className='react-select'
                classNamePrefix='select'
                options={statusOptions}
                value={currentTaskStatus}
                getOptionLabel={(option) => option.name}
                getOptionValue={(option) => option.id}
                onChange={async data => {
                  setCurrentTaskStatus(data)
                }}
              />
            </Col>
          }
          <div className='d-flex align-items-center mt-50 mt-lg-0 m me-50 me-lg-0'>
            <Input
              id='search-invoice'
              className='ms-0 m-lg-50 me-lg-2 me-0'
              type='text'
              value={value}
              onChange={e => handleFilter(e.target.value)}
              placeholder='Search Invoice'
            />
          </div>
          <Col className='d-flex flex-lg-nowrap align-items-center justify-content-start justify-content-lg-end flex-wrap mt-50 mt-lg-0 col-12 col-md-3 col-lg-3'>
          <Can I={'write'} a={'invoice'}>
            <Button tag={Link} to='/invoice/add' color='primary'>
              Add Invoice
            </Button>
          </Can>
          <UncontrolledDropdown className='ms-1'>
            <DropdownToggle color='secondary' caret outline>
              <MoreVertical className='font-small-4 me-50' />
            </DropdownToggle>
            <DropdownMenu>
              <Can I={'import'} a={'invoice'}>
                <DropdownItem tag={Link} to={`/import/invoice`} >
                  <Upload className='font-small-4 me-50' />
                  <span className='align-middle'>Import</span>
                </DropdownItem>
              </Can>
              <Can I={'export'} a={'invoice'}>
                <DropdownItem onClick={() => { exportList() }} >
                  <Download className='font-small-4 me-50' />
                  <span className='align-middle'>Export</span>
                </DropdownItem>
              </Can>
            </DropdownMenu>
          </UncontrolledDropdown>
        </Col>
      </Col>
    </Row>
    </div >
  )
}

const InvoiceList = () => {
  // ** Store vars

  const dispatch = useDispatch()
  const store = useSelector(state => state.invoice)
  const [block, setBlock] = useState(false)
  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }

  // ** States
  const [value, setValue] = useState('')
  const [sendSidebarOpen, setSendSidebarOpen] = useState(false)
  const [sort, setSort] = useState('desc')
  const [sortColumn, setSortColumn] = useState('createdon')
  const [selectedData, setSelectedData] = useState({})
  const [currentPage, setCurrentPage] = useState(1)
  const [rowsPerPage, setRowsPerPage] = useState(50)
  const [currentStatus, setCurrentStatus] = useState({ id: '', name: 'Select Status' })
  const [currentTaskStatus, setCurrentTaskStatus] = useState({ id: '', name: 'Select Task Status' })

  const toggleSendSidebar = () => setSendSidebarOpen(!sendSidebarOpen)

  const statusOptionsArr = ['', 'Pending', 'In progress', 'Completed', 'On Hold', 'Cancelled', 'Sent to Review', 'Request Changes']
  const [selecteIds, setSelectedIds] = useState([])
  // ** renders client column
  const renderClient = row => {
    const stateNum = Math.floor(Math.random() * 6),
      states = ['light-success', 'light-danger', 'light-warning', 'light-info', 'light-primary', 'light-secondary'],
      color = states[stateNum]
    return <Avatar color={color} className='me-50' content={row.contactname !== null ? row.contactname?.charAt(0) : ''} />

  }

  const dateFormat = (value) => {
    if (!value) return ''
    if (value.length === 13) {
      return moment(value, 'x').format("MMM DD, YYYY")
    } else {
      return moment.unix(value).format("MMM DD, YYYY")
    }

  }

  const renderStatus = (val) => {
    const obj = paymentstatusObj.find((o) => parseInt(o.id) === parseInt(val))
    return obj
  }
  const deleteInvoicefun = (id) => {

    return MySwal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      customClass: {
        confirmButton: 'btn btn-primary',
        cancelButton: 'btn btn-outline-danger ms-1'
      },
      buttonsStyling: false
    }).then(async (result) => {
      if (result.value) {
        await dispatch(deleteInvoice(id))
        MySwal.fire({
          icon: 'success',
          title: 'Deleted!',
          text: 'Invoice has been deleted.',
          customClass: {
            confirmButton: 'btn btn-success'
          }
        })
      }
    })
  }

  useEffect(async () => {
    setBlock(true)
    await dispatch(
      getData({
        sort,
        q: value,
        sortColumn,
        page: currentPage,
        perPage: rowsPerPage,
        status: currentStatus.id,
        taskstatus: currentTaskStatus.id
      })
    )
    setBlock(false)
  }, [dispatch, currentStatus, currentTaskStatus])

  const handleFilter = async val => {
    setValue(val)
    setBlock(true)
    await dispatch(
      getData({
        sort,
        q: val,
        sortColumn,
        page: currentPage,
        perPage: rowsPerPage,
        status: currentStatus.id,
        taskstatus: currentTaskStatus.id
      })
    )
    setBlock(false)
  }

  const handlePerPage = async e => {
    setBlock(true)
    await dispatch(
      getData({
        sort,
        q: value,
        sortColumn,
        page: currentPage,
        status: currentStatus.id,
        perPage: parseInt(e.target.value),
        taskstatus: currentTaskStatus.id
      })
    )
    setRowsPerPage(parseInt(e.target.value))
    setBlock(false)
  }

  const handlePagination = async page => {
    setBlock(true)
    await dispatch(
      getData({
        sort,
        q: value,
        sortColumn,
        status: currentStatus.id,
        perPage: rowsPerPage,
        page: page.selected + 1,
        taskstatus: currentTaskStatus.id
      })
    )
    setCurrentPage(page.selected + 1)
    setBlock(false)
  }

  const handleSort = async col => {

    setBlock(true)
    await dispatch(
      getData({
        sort,
        sortColumn: col,
        q: value,
        status: currentStatus.id,
        perPage: rowsPerPage,
        page: currentPage,
        taskstatus: currentTaskStatus.id
      })
    )
    setSort(sort === 'asc' ? 'desc' : 'asc')
    setSortColumn(col)
    setBlock(false)
  }

  const exportList = async () => {
    setBlock(true)
    await dispatch(
      exportInvoice({
        sort,
        sortColumn,
        q: value,
        status: currentStatus.id,
        taskstatus: currentTaskStatus.id
      })
    )
    setBlock(false)
  }

  const CustomPagination = () => {
    const count = Number((store.total / rowsPerPage).toFixed(0))

    return (
      <ReactPaginate
        nextLabel=''
        breakLabel='...'
        previousLabel=''
        pageCount={count || 1}
        activeClassName='active'
        breakClassName='page-item'
        pageClassName={'page-item'}
        breakLinkClassName='page-link'
        nextLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousLinkClassName={'page-link'}
        previousClassName={'page-item prev'}
        onPageChange={page => handlePagination(page)}
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        containerClassName={'pagination react-paginate justify-content-end p-1'}
      />
    )
  }

  const dataToRender = () => {
    const filters = {
      q: value,
      status: currentStatus.id
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })

    if (store.data.length > 0) {
      return store.data
    } else if (store.data.length === 0 && isFiltered) {
      return []
    } else {
      return []
    }
  }

  useEffect(() => {
    dataToRender()
  }, [store.data])

  const downloadPdf = async (row) => {
    setBlock(true)
    axios.post('/pdfmaker/pdf', { id: row.id })
      .then((res) => {
        const downloadLink = document.createElement("a")
        const fileName = `${row.uniqueno}.pdf`
        downloadLink.href = res.data.pdf
        downloadLink.download = fileName
        downloadLink.click()
        document.removeChild(downloadLink)
        setBlock(false)
      })
      .catch((err) => {
        console.log(err)
        setBlock(false)
      })
  }

  const stringTrimfn = (text) => {
    return text.length >= 20 ? `${text.substr(0, 20)}...` : text
  }

  const handleCheckBox = (e, id) => {
    if (e.target.checked) {
      setSelectedIds([...selecteIds, id])
    } else {
      const temp = [...selecteIds]
      const ind = temp.indexOf(id)
      temp.splice(ind, 1)
      setSelectedIds(temp)
    }

  }

  return (
    <div className='invoice-list-wrapper'>
      <UILoader blocking={block} loader={<Loader />}>
        <Card>
          <div className='invoice-list-dataTable react-dataTable'>
            <CustomHeader handlePerPage={handlePerPage} exportList={exportList} handleFilter={handleFilter} setCurrentTaskStatus={setCurrentTaskStatus} setCurrentStatus={setCurrentStatus} currentStatus={currentStatus} currentTaskStatus={currentTaskStatus} statusOptions={statusOptions} selecteIds={selecteIds} />
            <Table responsive className='table-hover'>
              <thead>
                <tr>
                  <th></th>
                  <th scope='col' className='text-nowrap' style={{ padding: '8px 7px', fontSize: '13px' }}>ID</th>
                  <th>
                    DRAFT / INVOICE
                  </th>
                  <th scope='col' className='text-nowrap' style={{ padding: '8px 8px', fontSize: '13px' }}>Customer</th>
                  <th scope='col' className='text-nowrap' style={{ padding: '8px 8px', fontSize: '13px' }}>Service</th>
                  <th scope='col' className='text-nowrap' style={{ padding: '8px 8px', fontSize: '13px' }} onClick={() => { handleSort('invoicedate') }}>Invoice Date</th>
                  <th scope='col' className='text-nowrap' style={{ padding: '8px 5px', fontSize: '13px' }} onClick={() => { handleSort('paymentdue') }}>Due Date</th>
                  <th scope='col' className='text-nowrap' style={{ padding: '8px 5px', fontSize: '13px' }}>Total</th>
                  <th scope='col' className='text-nowrap' style={{ padding: '8px 5px', fontSize: '13px' }}>Balance</th>
                  <th scope='col' className='text-nowrap' style={{ padding: '8px 5px', fontSize: '13px' }}>Payment Status</th>
                  {(parseInt(currentStatus.id) === 4 || currentStatus.id === '') && <th scope='col' className='text-nowrap'>Task Status</th>}
                  <th scope='col' className='text-nowrap' style={{ padding: '8px 5px', fontSize: '13px' }}>Action</th>
                </tr>
              </thead>
              <tbody>
                {dataToRender().map((row, index) => {
                  return (
                    <tr key={index}>
                      <td>
                        <Input type='checkbox' color='primary' value={row.id} onChange={(e) => { handleCheckBox(e, row.id) }} />
                      </td>
                      <td className='text-nowrap' style={{ padding: '8px 5px', fontSize: '13px' }}><Link to={`/${row.status === 4 ? "proposal" : "invoice"}/view/${row.id}`}>
                        <span className='text-body'>{`${row.uniqueno}`}</span></Link></td>
                      <td className='text-nowrap' style={{ width: '40px' }}>
                        <Link to={`/${row.status === 4 ? "proposal" : "invoice"}/view/${row.id}`}>
                          <UncontrolledTooltip target={`row${row.id}`}>{row.status === 4 ? 'Draft' : 'Invoice'}</UncontrolledTooltip>
                          <Avatar id={`row${row.id}`} color={row.status === 4 ? 'light-primary' : 'light-warning'} icon={row.status === 4 ? <Save size={14} /> : <Send size={14} />} className='me-2' />
                        </Link>
                      </td>
                      <td className='text-nowrap' style={{ padding: '8px 5px', fontSize: '13px' }}>{
                        <Link to={`/${row.status === 4 ? "proposal" : "invoice"}/view/${row.id}`}>
                          <div className='d-flex' style={{ padding: "0.2574rem 0rem" }}>
                            {renderClient(row)}
                            <div className='d-flex flex-column text-overflow-ellipsis'>
                              <h6 className='user-name text-truncate mb-0'>{stringTrimfn(row.contactname || '')}</h6>
                              <small className='text-truncate text-muted mb-0'>{stringTrimfn(row.contactemail || '')}</small>
                            </div>
                          </div>
                        </Link>
                      }</td>
                      <td className='text-nowrap' style={{ padding: '8px 0px', fontSize: '13px' }}><Link to={`/${row.status === 4 ? "proposal" : "invoice"}/view/${row.id}`}>
                        <span className='text-body text-truncate' style={{ padding: "0.2574rem 0rem" }}>{stringTrimfn(row.taskinvoiceitemsservicename || '')}
                        </span></Link></td>
                      <td className='text-nowrap' style={{ padding: '8px 5px', fontSize: '13px' }}><Link to={`/${row.status === 4 ? "proposal" : "invoice"}/view/${row.id}`}>
                        <span className='text-body'>{dateFormat(row.invoicedate)}</span></Link></td>
                      <td className='text-nowrap' style={{ padding: '8px 5px', fontSize: '13px' }}><Link to={`/${row.status === 4 ? "proposal" : "invoice"}/view/${row.id}`}>
                        <span className='text-body'>{dateFormat(row.paymentdue)}</span></Link></td>
                      <td className='text-nowrap' style={{ padding: '8px 5px', fontSize: '13px' }}><Link to={`/${row.status === 4 ? "proposal" : "invoice"}/view/${row.id}`}><span className='text-body'>{row.totalamount || 0}</span>
                      </Link></td>
                      <td className='text-nowrap' style={{ padding: '8px 5px', fontSize: '13px' }}>
                        {row.dueamount !== '0' ? (
                          <span>{row.dueamount}</span>
                        ) : (
                          <Badge color='light-success' pill>
                            Paid
                          </Badge>
                        )}
                      </td>
                      <td className='text-nowrap' style={{ padding: '8px 5px', fontSize: '13px' }}>
                        {row.status === 4 &&
                          <Badge color={'light-primary'}>Draft</Badge>
                        }{row.status !== 4 &&
                          <Badge color={renderStatus(row.paymentstatus)?.color || 'light-primary'}>{renderStatus(row.paymentstatus).name || row.paymentstatus}</Badge>
                        }
                      </td>
                      {(parseInt(currentStatus.id) === 4 || currentStatus.id === '') &&
                        <td className='text-nowrap' style={{ padding: '8px 5px', fontSize: '13px', width: '80px' }}>
                          <Badge color={statusObj[row.taskstatus]}>{statusOptionsArr[row.taskstatus]}</Badge>
                        </td>
                      }
                      <td className='text-nowrap' style={{ padding: '8px 5px', fontSize: '13px' }}>
                        <div className='column-action d-flex align-items-center'>

                          <Can I={'read'} a={'invoice'}><Send size={17} onClick={() => { toggleSendSidebar(); setSelectedData(row) }} /></Can>

                          <Can I={'read'} a={'invoice'}> <Link to={`/${row.status === 4 ? "proposal" : "invoice"}/view/${row.id}`} id={`pw-tooltip-${row.id}`}>
                            <Eye size={17} className='mx-1' />
                          </Link></Can>
                          <UncontrolledTooltip placement='top' target={`pw-tooltip-${row.id}`}>
                            Preview Invoice
                          </UncontrolledTooltip>
                          <UncontrolledDropdown>
                            <DropdownToggle tag='span'>
                              <MoreVertical size={17} className='cursor-pointer' />
                            </DropdownToggle>
                            <DropdownMenu end>
                              <Can I={'export'} a={'invoice'}>
                                <DropdownItem tag='a' href='/' className='w-100' onClick={e => { e.preventDefault(); downloadPdf(row) }}>
                                  <Download size={14} className='me-50' />
                                  <span className='align-middle'>Download</span>
                                </DropdownItem>
                              </Can>
                              <Can I={'write'} a={'invoice'}>
                                <DropdownItem tag={Link} to={`/invoice/edit/${row.id}`} className='w-100'>
                                  <Edit size={14} className='me-50' />
                                  <span className='align-middle'>Edit</span>
                                </DropdownItem>
                              </Can>
                              <Can I={'delete'} a={'invoice'}>
                                <DropdownItem
                                  tag='a'
                                  href='/'
                                  className='w-100'
                                  onClick={e => {
                                    e.preventDefault()
                                    deleteInvoicefun(row.id)
                                  }}
                                >
                                  <Trash size={14} className='me-50' />
                                  <span className='align-middle'>Delete</span>
                                </DropdownItem>
                              </Can>
                              <Can I={'write'} a={'invoice'}>
                                <DropdownItem tag='a' href={`/invoice/clone/${row.id}`} className='w-100' >
                                  <Copy size={14} className='me-50' />
                                  <span className='align-middle'>Duplicate</span>
                                </DropdownItem>
                              </Can>
                            </DropdownMenu>
                          </UncontrolledDropdown>
                        </div>
                      </td>
                    </tr>
                  )
                })
                }
              </tbody>
            </Table>
            <CustomPagination />
          </div>
          {sendSidebarOpen &&
            <SendInvoiceSidebar toggleSidebar={toggleSendSidebar} open={sendSidebarOpen} invoice={selectedData} />
          }
        </Card>
      </UILoader>
    </div>
  )
}

export default InvoiceList
