// ** React Imports
import { useState, useEffect, Fragment } from 'react'

// ** Custom Components
import Avatar from '@components/avatar'

// ** Third Party Components
import { Editor } from 'react-draft-wysiwyg'
import draftToHtml from 'draftjs-to-html'
import { EditorState, ContentState, convertFromHTML, convertToRaw } from 'draft-js'
import Select, { components } from 'react-select'
import CreatableSelect from 'react-select/creatable'

import { Minus, X, Maximize2, Paperclip, MoreVertical, Trash, File } from 'react-feather'
import axios from '@src/configs/axios/axiosConfig'
// ** Reactstrap Imports
import {
  Form,
  Label,
  Input,
  Modal,
  Button,
  ModalBody
} from 'reactstrap'

// ** Utils
import { selectThemeColors } from '@utils'
import moment from 'moment'
// ** User Avatars
import { activeOrganization, loggedInUser } from '@src/helper/sassHelper'
const activeOrg = activeOrganization()
const userData = loggedInUser()
// ** Styles
import '@styles/react/libs/editor/editor.scss'
import '@styles/react/libs/react-select/_react-select.scss'
import UILoader from '@components/ui-loader'
import Spinner from '@components/spinner/Loading-spinner'
import toast from 'react-hot-toast'
const SidebarSendInvoice = props => {

  const colourStyles = {
    multiValue: (styles, { }) => {

      return {
        ...styles,
        backgroundColor: '#7367f0'
      }
    }
  }

  const [block, setBlock] = useState(false)

  const Loader = () => {
    return (
      <Fragment>
        <Spinner />
      </Fragment>
    )
  }
  const [toEmail, setToEmail] = useState([])
  const [ccEmail, setCCEmail] = useState([])
  const [data, setData] = useState('')
  const { open, toggleSidebar, invoice } = props
  const [thankyouNote, setThankyouNote] = useState('')
  const [userOptions, setUserOptions] = useState([])
  const [clientOptions, setClientOptions] = useState([])

  const getOrganizationUsers = () => {
    axios.post('/organizationusers/dropdown').then((res) => {
      setUserOptions(res.data.organizationusers)
    }).catch(() => { })
  }

  const getCoatctInfo = (id) => {
    axios.post('contactinformations/list', { contactid: id }).then((res) => {
      setClientOptions(res.data.contactinformations)
    }).catch(() => { })

  }

  const dateFormat = (value) => {
    if (value.length === 13) {
      return moment(value, 'x').format("MMM DD, YYYY")
    } else {
      return moment.unix(value).format("MMM DD, YYYY")
    }

  }

  const getThankyouNotes = () => {
    axios.post('/emailtemplates/get', { slug: 'invoice-send-mail' }).then((res) => {
      const mailSub = res.data.emailtemplates.description
      const test = mailSub.replace('{invoice_date}', dateFormat(invoice.paymentdue)).replace('{service}', invoice.taskinvoiceitemsservicename).replace('recipient_name', invoice.contactperson).replace('{recipient_name}', invoice.contactperson).replace('{invoice_no}', invoice.uniqueno).replace('{amount}', invoice.totalamount).replace('{invoice_amount}', invoice.totalamount).replace('{your_name}', userData.name).replaceAll('{firm_name}', activeOrg.name).replace('{', '').replace('}', '')

      const temp = EditorState.createWithContent(
        ContentState.createFromBlockArray(
          convertFromHTML(test)
        )
      )
      setData(temp)
      setThankyouNote(test)

    }).catch(() => { })
  }

  const handleChange = (editorState) => {
    setData(editorState)
    setThankyouNote(draftToHtml(convertToRaw(editorState.getCurrentContent())))
  }


  useEffect(() => {
    if (invoice.clientid !== undefined) {
      getCoatctInfo(invoice.clientid)
    }
    if (invoice.contactid !== undefined) {
      getCoatctInfo(invoice.contactid)
    }
    if (Object.keys(invoice).length > 0) {
      getOrganizationUsers()
      getThankyouNotes()
    }
  }, [invoice])

  const sendMail = () => {
    setBlock(true)
    const subject = `${invoice.uniqueno} Invoices Acknowledgement Mail`
    axios.post('/google/sendGmail', { content: thankyouNote, subject, toEmail, ccEmail, taskinvoiceId: invoice.id, clientName: invoice.contactname })
      .then(() => {
        toggleSidebar(false)
        setBlock(false)
        toast.success('Email send Successfully', { position: 'bottom-right' })
      })
    console.log(() => { })
  }


  // ** States
  const [ccOpen, setCCOpen] = useState(false)

  const SelectComponent = ({ data, ...props }) => {
    return (
      <components.Option {...props}>
        <div className='d-flex flex-wrap align-items-center'>
          <Avatar className='my-0 me-50' size='sm' initials content={data.name?.charAt(0)} />
          {data.name} - {data.email}
        </div>
      </components.Option>
    )
  }

  // ** CC Toggle Function
  const toggleCC = e => {
    e.preventDefault()
    setCCOpen(!ccOpen)
  }

  // ** Toggles Compose POPUP
  const togglePopUp = e => {
    e.preventDefault()
    toggleSidebar()
  }

  return (

    <div className='email-application'>
      <div className='content-area-wrapper'>
        <div className='col-md-12'>
          <div className='content-body'>
            <Modal
              scrollable
              fade={false}
              keyboard={false}
              backdrop={false}
              id='compose-mail'
              container='.content-body'
              className='modal-lg'
              isOpen={open}
              contentClassName='p-0'
              toggle={toggleSidebar}
              modalClassName='modal-sticky'
            >
              <UILoader blocking={block} loader={<Loader />}>
                <div className='modal-header'>
                  <h5 className='modal-title'>Compose Mail</h5>
                  <div className='modal-actions'>
                    {/* <a href='/' className='text-body me-75' onClick={togglePopUp}>
                      <Minus size={14} />
                    </a>
                    <a href='/' className='text-body me-75' onClick={e => e.preventDefault()}>
                      <Maximize2 size={14} />
                    </a> */}
                    <a href='/' className='text-body' onClick={togglePopUp}>
                      <X size={14} />
                    </a>
                  </div>
                </div>
                <ModalBody className='flex-grow-1 p-0'>
                  <Form className='compose-form' onSubmit={e => e.preventDefault()}>
                    <div className='compose-mail-form-field'>
                      <Label for='email-to' className='form-label'>
                        To:
                      </Label>
                      <div className='flex-grow-1'>
                        <CreatableSelect
                          isMulti
                          id='email-to'
                          isClearable={false}
                          theme={selectThemeColors}
                          options={clientOptions}
                          className='react-select select-borderless'
                          classNamePrefix='select'
                          components={{ Option: SelectComponent }}
                          styles={colourStyles}
                          onChange={(e) => { setToEmail(e.map(i => i.email)) }}
                          getOptionLabel={(option) => `${option.name} - ${option.email}`}
                          getOptionValue={(option) => option.id}
                        />
                      </div>
                      <div>
                        <a href='/' className='toggle-cc text-body me-1' onClick={toggleCC}>
                          Cc
                        </a>
                      </div>
                    </div>
                    {ccOpen === true ? (
                      <div className='compose-mail-form-field cc-wrapper'>
                        <Label for='email-cc' className='form-label'>
                          Cc:
                        </Label>
                        <div className='flex-grow-1'>
                          <Select
                            isMulti
                            id='email-cc'
                            onChange={(e) => { setCCEmail(e.map(i => i.email)) }}
                            isClearable={false}
                            theme={selectThemeColors}
                            options={userOptions}
                            className='react-select select-borderless'
                            classNamePrefix='select'
                            components={{ Option: SelectComponent }}
                            styles={colourStyles}
                            getOptionLabel={(option) => `${option.name} - ${option.email}`}
                            getOptionValue={(option) => option.id}
                          />
                        </div>
                        <div>
                          <a href='/' className='toggle-cc text-body' onClick={toggleCC}>
                            <X size={14} />
                          </a>
                        </div>
                      </div>
                    ) : null}
                    <div className='compose-mail-form-field'>
                      <Label for='email-subject' className='form-label'>
                        Subject:
                      </Label>
                      <Input id='email-subject' placeholder='Subject' />
                    </div>
                    <div id='message-editor'>
                      <Editor
                        placeholder='Message'
                        toolbarClassName='rounded-0'
                        wrapperClassName='toolbar-bottom'
                        editorState={data}
                        onEditorStateChange={handleChange}
                        editorClassName='rounded-0 border-0'
                        toolbar={{
                          options: ['inline', 'textAlign'],
                          inline: {
                            inDropdown: false,
                            options: ['bold', 'italic', 'underline', 'strikethrough']
                          }
                        }}
                      />
                    </div>
                    <div className='compose-footer-wrapper'>
                      <div className='btn-wrapper d-flex align-items-center'>
                        <Button outline color='primary' onClick={() => { sendMail() }}>Send</Button>
                        <div className='email-attachement rounded border p-50 ms-50'>
                          {invoice.uniqueno}.pdf
                        </div>
                      </div>
                      <div className='footer-action d-flex align-items-center'>
                        <Trash className='cursor-pointer' size={18} onClick={toggleSidebar} />
                      </div>
                    </div>
                  </Form>
                </ModalBody>
              </UILoader>
            </Modal>
          </div></div></div>
    </div>

  )
}

export default SidebarSendInvoice
